import Vue from 'vue'

export default ({ endpoint, client_id, client_secret }) => ({

    state: {
        plain: null,
        encrypted: null,
    },

    mutations: {
        'security/plain': (state, plain) => {
            state.plain = plain
        },
        'security/encrypted': (state, encrypted) => {
            state.plain = null
            state.encrypted = encrypted
        }
    },

    actions: {

        'security/signin': ({ commit, state }, data) => {

            var fd = new FormData();
            fd.append('grant_type', 'password');
            fd.append('client_id', client_id);
            fd.append('client_secret', client_secret);
            fd.append('username', data.username);
            fd.append('password', data.password);

            return Vue.http.post(`${endpoint}/oauth/v2/token`, fd)
        },
    }
})



